import styles from './styles.module.scss'
import React, {FC} from "react"

interface RoomTypesProps {
    setRoomType: (roomType: string) => void;
}

const RoomTypes: FC<RoomTypesProps> = ({ setRoomType }) => {

    return (
        <div className={styles.container}>
            <div className={styles.roomTypesHead}> :</div>
            <div className={styles.roomTypesList}>
                <button onClick={() => setRoomType('Standard')}></button>
                <button onClick={() => setRoomType('Luxe')}></button>
                <button onClick={() => setRoomType('Deluxe')}></button>
            </div>
        </div>
    )
}

export default RoomTypes;